
import React, {Component} from 'react';
import axios from 'axios';
import ButtonHome from '../partials/ButtonHome';
import funciones from '../funciones';

import { Bar } from 'react-chartjs-2';

let api = 'https://mercadosefectivos-bi-api.herokuapp.com';



export default class ViewAnalisisEmpresa extends Component {
    constructor(props) {
        super(props);
        this.state = {
           loading: false,
           chartvisible:true,
           codmarca:0,
           desmarca:"",
           universo:0,
           tblMarcas:[],
           tblMarcaMunicipios:[],
           tblMarcaVendedores:[],
           tblMarcaFechas:[]
        }
    }

    componentDidMount() {
        this.getTblMarcas();
    }

    getDataMarca=(codmarca,desmarca)=>{
        

            this.setState({chartvisible:false,codmarca:codmarca,desmarca:desmarca});
           
                this.getMarcaDetalles();
         
            
    }

    getTblMarcas=()=>{
        this.setState({"loading":true});

        axios.get(`${api}/getmarcas?empnit=${this.props.empnit}&anio=${2021}&mes=${9}`)
        //axios.post(`${api}/getmarcas`, {empnit:this.props.empnit,anio:2021,mes:9})
        .then(res => {
          const marcas = res.data.recordset;
          //console.log(empresas);
          this.setState({"tblMarcas": marcas,"loading":false});
          //return empresas;
        })
        .catch(()=>{
            //return [];
            this.setState({"tblMarcas": [],"loading":false});
        })
    }

    getMarcaDetalles=()=>{
       
        this.setState({"loading":true});

        //obtiene marcas por fecha
        axios.get(`${api}/getmarcasfecha?empnit=${this.props.empnit}&anio=${2021}&mes=${9}&codmarca=${this.state.codmarca}`)
        .then(res => {
          const marcafechas = res.data.recordset;
          //console.log(empresas);
          this.setState({"tblMarcaFechas": marcafechas,"loading":false});
          //return empresas;
        })
        .catch(()=>{
            //return [];
            this.setState({"tblMarcaFechas": [],"loading":false});
        })

         //obtiene marcas por municipio
         axios.get(`${api}/getmarcasmunicipio?empnit=${this.props.empnit}&anio=${2021}&mes=${9}&codmarca=${this.state.codmarca}`)
        .then(res => {
        const marcamunicipios = res.data.recordset;
        //console.log(empresas);
        this.setState({"tblMarcaMunicipios": marcamunicipios,"loading":false});
        //return empresas;
        })
        .catch(()=>{
            //return [];
            this.setState({"tblMarcaMunicipios": [],"loading":false});
        })

    
        //obtiene marcas por vendedor
        axios.get(`${api}/getmarcasvendedor?empnit=${this.props.empnit}&anio=${2021}&mes=${9}&codmarca=${this.state.codmarca}`)
        .then(res => {
        const marcavendedores = res.data.recordset;
        //console.log(empresas);
        this.setState({"tblMarcaVendedores": marcavendedores,"loading":false});
        //return empresas;
        })
        .catch(()=>{
            //return [];
            this.setState({"tblMarcaVendedores": [],"loading":false});
        })




    }

    setBack =() => {
        this.props.vista("INICIO");
        //this.props.empnit
    }

    rand = () => {
        return Math.floor(Math.random() * 255)
    };

    render() {
       
            const dataM = this.state.tblMarcas;

            let label = []; let valor = []; let bgColor = []; let i =0;
            let data = this.state.tblMarcas;
            data.map((r)=>{
                    label.push(r.DESMARCA);
                    valor.push(Number(r.TOTALPRECIO));
                    bgColor.push( `rgb(${this.rand()}, ${this.rand()}, ${this.rand()})`)
            })
            let bardata = {
                labels: label,
                datasets: [
                {
                    data:valor,
                    backgroundColor:bgColor,
                    borderWidth: 1,
                },
                ],
            };

            if(this.state.chartvisible==true){
                return(
                    <div>
                        <div className="row">
                            <div className="card shadow border-top-rounded border-bottom-rounded col-sm-12 col-md-6 col-xl-6 col-lg-6">
                                <div className="card-header">
                                    <h3>Ventas por Marca</h3>
                                    <button className="btn btn-outline-secondary btn-md btn-circle" onClick={()=>{this.getTblMarcas()}}>
                                        <i className="fas fa-sync"></i>
                                    </button>
                                </div>
                                <div className="card-body">
                                    
                                    <Bar height={400} data={bardata} options={{
                                indexAxis:'y',
                                legend: {
                                display: true,
                                position: "right"
                                },
                                onClick: function(evt, element) {
                                    //console.log(evt.chart);
                                    try {
                                        evt.chart.legend.legendItems.map((item) => {
                                            if(item.index==element[0].element.$context.dataIndex){
                                                console.log(item.text);
                                                console.log(element[0].element.$context.parsed)  
                                            }
                                        })
                                        //console.log(element[0].element.$context.dataIndex)
                                        //console.log(element[0].element.$context.parsed)  
                                    } catch (error) {
                                        
                                    }
                                  
                                }
                            }} />
                       
                                   
                                </div>
                            </div> 
                            <div className="card shadow border-top-rounded border-bottom-rounded col-sm-12 col-md-6 col-xl-6 col-lg-6">
                                <div className="table-responsive">
                                    <table className="table table-hover ">
                                        <thead className="bg-secondary text-white ">
                                            <tr>
                                                <td>Marca</td>
                                                <td>Importe</td>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {dataM.map((r)=>{
                                                return(
                                                    <tr className="hand" key={`row-${r.CODMARCA}`} onClick={()=>{this.getDataMarca(r.CODMARCA,r.DESMARCA)}}>
                                                        <td>{r.DESMARCA}</td>
                                                        <td>{funciones.setMoneda(r.TOTALPRECIO,'Q')}</td>
                                                    </tr>
                                                )
                                            })}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                        <div className="">
                            <ButtonHome vista={this.setBack} />    
                        </div>
                        
                    </div>
                    )
            }else{
                //obtengo los valores
               
                return(
                    <div>
                        <div className="card shadow border-top-rounder">
                            <div className="card-header bg-secondary text-white">
                                <h3>Detalle de la Marca {this.state.desmarca}</h3>
                                <button className="btn btn-sm btn-secondary border-white" onClick={()=>{this.setState({chartvisible:true})}}>
                                   <i className="fas fa-arrow-left"></i>Regresar
                                </button>
                            </div>
                            <div className="card-body">
                                <ul>
                                    {this.state.tblMarcaMunicipios.map((r)=>{
                                        return `<li>${r.MUNICIPIO}</li>`
                                    })}
                                </ul>
                            </div>
                        </div> 
                        <ButtonHome vista={this.setBack} />
                    </div>
                    )
            }
   
    }
};